}

img {
  height: 1rem;
  width: 1rem;
}

:root {
  --main-color: #6D89B9;
  --color-transparent: transparent;

  --principal-gradient: linear-gradient(90deg, #607081, #313E4C);

  --secondary-gradient-hover: linear-gradient(90deg, #ABD0C5, #3C705A);
  --secondary-gradient-active: linear-gradient(90deg, #BCE0D6, #4D816B);

  --bd-radius-1rem: 1rem;
  --bd-radius-0_5rem: .5rem;
  --bd-radius-0_7rem: .7rem;

  --bx-shadow-task: 2px 3px 7px 1px #3B453D;
  --bx-shadow-task-inverted: -2px 3px 7px 1px #3B453D;
  --bx-shadow-bottom: 0 1px 6px 1px #666666;
}



/****************************************************/

