/*////////////////*/

html, body {
  color: #2B2E31;
  margin: 0;
  overflow-x: hidden;
  padding: 0;
  /*background: url('./Assets/BackgroundIMG.jpg'), no-repeat center center;
  background-size: //cover;*/
}

button {
  background-color: var(--color-transparent);
  border: 1px solid var(--color-transparent);
  cursor: pointer;
}

h1, h2, h3, h4, h5, h6, p, img, button, div {
  transition: all ease-in-out .2s;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

.tilt {
  transition: none;
}

.tilt:hover {
  transform: scale(0);
}

.bold {
  font-weight: 900;
}

button:hover {
  border-radius: var(--bd-radius-0_5rem);
  transform: scale(1.1);
}

button:active {
  opacity: .5;
  transform: scale(1.02);
}

.underline {
  text-decoration: underline;
}

/*          Home          */

.header {
  align-items: center;
  animation: open-opacity .2s;
  background-color: #EAEAEA;
  box-shadow: var(--bx-shadow-bottom);
  cursor: default;
  height: 8%;
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding: .2rem 0;
  position: fixed;
  top: 0;
  z-index: 500;
}

.header-logo-container h1 {
  animation: open-left .5s;
  color: #356495;
  font-family: 'Rubik Maps', system-ui;
  font-size: 2.8rem;
  font-weight: 100;
  letter-spacing: -.38rem;
  margin: 0;
}

.header-buttons-container button {
  animation: open-right .5s;
  font-family: 'Lemon', serif;
  font-size: 1.4rem;
  letter-spacing: -.2rem;
  margin: 0 1rem;
}

/*          About          */

.about {
  align-items: center;
  animation: open-opacity .5s;
  background-color: #F2F2F2;
  box-shadow: var(--bx-shadow-bottom);
  display: flex;
  justify-content: center;
  margin: 8vh 0 0 0;
  padding: 3rem 0;
  position: relative;
  width: 100vw;
  z-index: 100;
}

.about-me {
  align-items: center;
  animation: open-bottom .5s;
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
  top: 7%;
}

.about-me div {
  align-items: center;
  background-color: #C8CBCD;
  border-radius: 1rem;
  box-shadow: var(--bx-shadow-task);
  cursor: default;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1rem;
}

.about-me div h2 {
  font-family: 'Tektur', sans-serif;
  font-size: 2.5rem;
  letter-spacing: .2rem;
}

.about-me div img {
  height: 60%;
  margin: 1rem 0;
  width: 60%;
}

.about-me div img:hover {
  transform: scale(1.1);
}

.about-me div h4 {
  color: #356495;
  font-family: 'Righteous', sans-serif;
  font-size: 3rem;
  letter-spacing: -.2rem;
  margin: 1rem 0 0;
}

.about-me div p {
  font-family: 'Teko', sans-serif;
  font-size: 1.6rem;
}

.about-me button {
  background-color: #4670AB;
  border-radius: 1.2rem;
  box-shadow: var(--bx-shadow-bottom);
  font-family: 'Lemon', serif;
  font-size: 1.5rem;
  margin: 2.5rem 0 0;
  padding: 1rem 3rem;
}

.more-about-me {
  align-items: start;
  animation: open-bottom .5s;
  background-color: #EAEAEA;
  border-radius: var(--bd-radius-1rem);
  box-shadow: var(--bx-shadow-task);
  cursor: default;
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  width: 90%;
}

.new-about-me {
  width: 28%;
}

.new-about-me div h2 {
  font-size: 1.2rem;
}

.new-about-me div h4 {
  font-size: 2.1rem;
}

.new-about-me div p {
  font-size: 1.4rem;
}

.new-about-me div img {
  height: 50%;
  width: 50%;
}

.more {
  width: 66%;
  border-left: 3px solid #000;
  display: flex;
  flex-direction: column;
  font-family: 'Tektur', sans-serif;
  font-size: 3rem;
  padding: 0 0 0 1rem;
}

.more-about-me-resume h3 {
  animation: open-bottom .5s;
  text-align: center;
  font-family: 'Tektur', sans-serif;
  font-size: 2.5rem;
}

.more-about-me-resume p {
  animation: open-bottom .5s;
  font-family: 'Unbounded', sans-serif;
  font-size: 1.1rem;
  letter-spacing: -.07rem;
  margin: .5rem 0;
}

.more h5 {
  margin: 2rem 0 .2rem;
}

.lines {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.lines div {
  border: 1px solid #000;
  height: 1px;
  width: 5%;
}

.more > div > h6 {
  border-bottom: 2px solid #000;
  margin: 2rem 0 0;
}

.more > div > div {
  align-items: center;
  display: flex;
  justify-content: start;
}

.more > div > div > img {
  height: 5rem;
  width: 5rem;
  margin: 0 2rem 0 0;
}

.more-button-container {
  align-items: center;
  display: flex;
  justify-content: end;
}

.more-button-container button {
  background-color: #4670AB;
  border-radius: var(--bd-radius-1rem);
  font-family: 'Lemon', serif;
  font-size: 1.5rem;
  margin: 2rem 0;
  padding: 1rem 2rem;
}

/*          ProjectsPage          */

.projects-page {
  align-items: center;
  animation: open-opacity .2s;
  background-color: #F2F2F2;
  box-shadow: var(--bx-shadow-bottom);
  display: flex;
  justify-content: center;
  margin: 8vh 0 0 0;
  padding: 3rem 0;
  position: relative;
  width: 100vw;
  z-index: 100;
}

.projects-page-div {
  animation: open-bottom .5s;
  background-color: #EAEAEA;
  border-radius: var(--bd-radius-1rem);
  box-shadow: var(--bx-shadow-task);
  cursor: default;
  padding: 1rem;
  width: 90%;
}

.projects-page-div h4 {
  animation: open-bottom .5s;
  font-family: 'Black Ops One', system-ui;
  font-size: 3rem;
  text-align: center;
}

.project {
  align-items: center;
  display: flex;
  justify-content: space-around;
  padding: 1rem;
  margin: 5rem 0;
  transition: all ease-in-out .2s;
}

.project:hover {
  background-color: #F2F2F2;
  border-radius: var(--bd-radius-0_5rem);
  box-shadow: var(--bx-shadow-task);
  transform: scale(1.02);
}

.project > div h5 {
  border-bottom: 3px solid #000;
  font-family: 'Tektur', sans-serif;
  font-size: 2rem;
  letter-spacing: -.1rem;
  margin: .5rem 0;
  padding: 0 0 .7rem;
  text-align: center;
}

.project > div p {
  font-family: 'Unbounded', sans-serif;
  letter-spacing: -.07rem;
  margin: .5rem 0;
}

.project-text-div {
  border-radius: var(--bd-radius-1rem);
  box-shadow: var(--bx-shadow-task-inverted);
  padding: 1rem;
}

.project-img-div {
  border-radius: var(--bd-radius-0_5rem);
  box-shadow: var(--bx-shadow-task);
  padding: 1rem;
}

.project-buttons-container {
  align-items: center;
  display: flex;
  justify-content: space-around;
  margin: 2rem 0 0;
}

.project-buttons-container button {
  border-radius: var(--bd-radius-0_5rem);
  font-family: 'Lemon', serif;
  padding: .5rem 1rem;
}

.code-button {
  border: 2px solid #2B2E31;
  color: #2B2E31;
}

.code-button:hover {
  background-color: #2B2E31;
  color: #F2F2F2;
}

.live-button {
  border: 2px solid #4670AB;
  color: #4670AB;
}

.live-button:hover {
  background-color: #4670AB;
  color: #F2F2F2;
}

.project-left-text, .project-right-text {
  width: 40%;
}

.project-left-img, .project-right-img {
  width: 48%;
}

.project-left-img img, .project-right-img img {
  border-radius: var(--bd-radius-0_5rem);
  box-shadow: var(--bx-shadow-bottom);
  height: 100%;
  width: 100%;
}

.vertical-lines {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.vertical-lines div {
  border: 1px solid #000;
  height: .7rem;
  margin: .5rem 0;
  width: 1px;
}

.more-about-me:hover, .projects-page-div:hover,
.project-text-div:hover, .project-img-div:hover {
  border-radius: var(--bd-radius-0_5rem);
  transform: scale(1.02);
}

.more > div > div > img:hover {
  transform: scale(1.07);
}

/*          Footer          */

.footer {
  align-items: center;
  background-color: #EAEAEA;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2rem 0;
  position: relative;
  width: 100vw;
  z-index: 10;
}

.footer p {
  cursor: default;
  font-size: 1.1rem;
  margin: 0 0 2rem;
}

.footer div {
  align-items: center;
  display: flex;
  justify-content: center;
}

.footer div span {
  align-items: center;
  background-color: #F2F2F2;
  border-radius: 50%;
  box-shadow: var(--bx-shadow-task);
  cursor: pointer;
  display: flex;
  justify-content: center;
  height: 2rem;
  margin: 0 .5rem;
  width: 2rem;
}

.footer div span img {
  height: 70%;
  width: 70%;
}